.table-scrollable-container:after {
  content: "";
  display: table;
  clear: both;
}
.table-scrollable-container table {
  display: block;
  position: relative;
  border-top: 0 none transparent;
}
.table-scrollable-container thead,
.table-scrollable-container tbody {
  display: block;
}
.table-scrollable-container thead {
  width: 40%;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
}
.table-scrollable-container thead tr {
  width: 100%;
  display: block;
}
.table-scrollable-container thead th {
  display: block;
  height: 7rem;
  font-size: 1.2em;
}
.table-scrollable-container tbody {
  white-space: nowrap;
  padding-left: 40%;
  overflow-x: scroll;
}
.table-scrollable-container tbody tr {
  width: 90%;
  white-space: normal;
  display: inline-block;
  margin-right: -0.125em;
}
.table-scrollable-container tbody td {
  display: block;
  width: 100%;
  height: 7rem;
}
.table-scrollable-container th,
.table-scrollable-container td {
  padding: 0.5rem 1rem 0.25rem 1rem;
}
.table-scrollable-container .instructions {
  width: 100%;
  text-align: center;
  display: block;
  padding: 0.25rem 1rem;
  font-size: 0.8333333333em;
}
.table-scrollable-container .instructions .fa {
  margin: 0 0.25rem;
}

.table-scrollable .parent-cell {
  font-size: 1.2em;
}
@media print {
  .table-scrollable-container .table-scrollable .parent-cell {
    padding: 1em;
  }
  .table-scrollable-container .instructions {
    display: none;
  }
  .table-scrollable-container table {
    width: 100%;
    position: relative;
    clear: both;
    display: table;
  }
  .table-scrollable-container table * {
    width: auto;
    height: auto;
    margin: 0;
    padding: 0;
    position: relative;
  }
  .table-scrollable-container table thead,
.table-scrollable-container table tbody,
.table-scrollable-container table tfoot {
    display: table-row-group;
  }
  .table-scrollable-container table tr {
    display: table-row;
  }
  .table-scrollable-container table td,
.table-scrollable-container table th {
    display: table-cell;
    padding: 5px;
  }
}
